.QuizList {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    padding-top: 100px;
    -ms-flex-positive: 1;
        flex-grow: 1;
    width: 100%;
    background-image: -webkit-gradient(linear, left bottom, right top, from(#051937), color-stop(#004d7a), color-stop(#008793), color-stop(#00bf72), to(#a8eb12));
    background-image: -webkit-linear-gradient(left bottom, #051937, #004d7a, #008793, #00bf72, #a8eb12);
    background-image: -o-linear-gradient(left bottom, #051937, #004d7a, #008793, #00bf72, #a8eb12);
    background-image: linear-gradient(to right top, #051937, #004d7a, #008793, #00bf72, #a8eb12);
}

.QuizList h1 {
    color: #fff;
}

.QuizListUl {
    list-style: none;
    margin: 0;
    padding: 0;
}

.QuizListUl li {
    margin-bottom: 10px;
}

.QuizListUl li a {
    text-decoration: none;
    color: #fff;
}

.QuizListUl li a:hover {
    color: #363F54;
}


